.slider {
  position: relative; }
  .slider__scale {
    height: 0.1rem;
    margin: 3.5rem 0 0.7rem;
    border: 1rem solid #ffffff;
    border-left: none;
    border-right: none;
    background: #cccccc;
    cursor: pointer; }
    .slider__scale_vertical {
      width: 0.1rem;
      min-height: 35rem;
      margin: 0.5rem 0.4rem;
      border: 1rem solid #ffffff;
      border-top: none;
      border-bottom: none; }
  .slider__steps {
    position: relative;
    display: none; }
    .slider__steps_vertical {
      min-height: 35rem;
      position: absolute;
      top: 0;
      left: 2.7rem; }
    .slider__steps_visible {
      display: block; }
  .slider__step {
    position: absolute;
    margin: 0;
    font-size: 0.86rem;
    transform: translateX(-50%);
    cursor: pointer; }
    .slider__step_vertical {
      transform: translateY(-50%); }
  .slider__handle {
    position: absolute;
    top: 0;
    width: 1.43rem;
    height: 1.43rem;
    background: #ffffff;
    box-shadow: 0 0.07rem 0.43rem 0 rgba(32, 33, 36, 0.28);
    border-radius: 50%;
    transform: translate(-50%, 25%);
    cursor: pointer; }
    .slider__handle_vertical {
      transform: translate(50%, -50%); }
    .slider__handle_hidden {
      display: none; }
  .slider__bubble {
    display: none;
    width: 2rem;
    padding: 0.43rem;
    transform: translate(-25%, -120%);
    box-shadow: 0 0.07rem 0.43rem 0 rgba(32, 33, 36, 0.28);
    background: #ffffff;
    text-align: center;
    -moz-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    -webkit-user-select: none;
    user-select: none; }
    .slider__bubble_vertical {
      transform: translate(2.85rem, -0.36rem);
      box-shadow: 0 0.07rem 0.43rem 0 rgba(32, 33, 36, 0.28);
      text-align: center; }
    .slider__bubble_visible {
      display: block; }
